<template>
   <div class="big" :class="{in:!isa,out:isa}">
     <img src="../assets/jt.png" alt="" class="imgarr" :class="{left:isa,right:!isa}" @click="isa=!isa">
    <div class="ondata" ref="ondata"></div>

    <div class="ondata ondata2" ref="ondata2"></div>
  </div>
</template>

<script>
  import * as echarts from "echarts"
export default {
  name: 'HelloWorld',
  data () {
    return {
      isa:false
    }
  },
  components:{
  },
   mounted() {
      this.init()
      this.init2()
   },
   methods:{
     init(){
       let type = ['肥料', '农药'];
        let mychar = echarts.init(this.$refs.ondata);
        mychar.setOption({
            backgroundColor: '#081736',
              title: {
                  show: true,
                  text: '',
                  textStyle: {
                      align: 'rigth',
                      color: '#115CB9',
                      fontSize: 20,
                  },
                  top: '3%',
                  left: '5%',
              },
              grid: {
                  top: "30%",
                  right: "10%",
                  bottom: "18%",//也可设置left和right设置距离来控制图表的大小
              },
              tooltip: {
                  trigger: "axis",
                  backgroundColor: 'rgba(17,95,182,0.5)',
                  textStyle: {
                      color: "#fff"
                  },
                  // axisPointer: {
                  //     type: "line",
                  //     label: {
                  //         show: false,
                  //     }
                  // },
                  formatter: (pa) => {
                      let oneDotHtml = '<span style="display:inline-block;margin-right:5px;width:10px;height:10px;background-color:#1480C2"></span>'
                      let twoDotHtml = '<span style="display:inline-block;margin-right:5px;width:10px;height:10px;background-color:#8957A1"></span>'
                      // return pa[0].name + pa[0].value
                      return (
                          pa[0].name + '<br/>'
                          + oneDotHtml + pa[1].seriesName + '：' + pa[1].value + "<br>"
                          + twoDotHtml + pa[0].seriesName + '：' + pa[0].value
                      );
                  }
              },
              dataZoom: [{
                  show: false,
                  xAxisIndex: 0,
                  type: 'slider',
                  startValue: 0,
                  endValue: this.end
              }],
              legend: {
                  top: "10%",
                  left: '40%',
                  // icon: 'rect',
                  textStyle: {
                      padding: [0, 0, 0, 10],
                      color: "#ffffff",
                      fontSize: 14,
                      lineHeight: 16
                  },
                  itemGap: 50,
                  itemHeight: 10,
                  data: type,
              },
              xAxis: {
                  data: [
                      "草镇",
                      "白镇",
                      "衣镇",
                      "乐镇",
                      "永镇",
                      "安镇"
                  ],
                  axisLine: {
                      show: true, //隐藏X轴轴线
                      lineStyle: {
                          color: '#85B1B4'
                      }
                  },
                  axisTick: {
                      show: false //隐藏X轴刻度
                  },
                  axisLabel: {
                      show: true,
                      textStyle: {
                          padding: [5, 0, 0, 0],
                          color: "#fff" //X轴文字颜色
                      }
                  },
              },
              yAxis: [{
                  type: "value",
                  name: "吨",
                  nameTextStyle: {
                      color: "#fff",
                      fontSize: 14
                  },
                  splitLine: {
                      show: false,
                      lineStyle: {
                          color: 'rgba(33,35,98,1)'
                      }
                  },
                  axisTick: {
                      show: false
                  },
                  axisLine: {
                      show: true,
                      lineStyle: {
                          color: '#85B1B4'
                      }
                  },
                  axisLabel: {
                      show: true,
                      textStyle: {
                          fontSize: 14,
                          color: "#fff"
                      }
                  },
              },
              {
                  type: "value",
                  // name: "同比",
                  // nameTextStyle: {
                  //     show: false,
                  //     color: "#ebf8ac"
                  // },
                  position: "right",
                  splitLine: {
                      show: false
                  },
                  axisTick: {
                      show: false
                  },
                  axisLine: {
                      show: false
                  },
                  axisLabel: {
                      show: false,
                      textStyle: {
                          color: "#85B1B4"
                      }
                  }
              },
                  // {
                  //     type: "value",
                  //     // gridIndex: 1,
                  //     min: 50,
                  //     max: 100,
                  //     splitNumber: 8,
                  //     splitLine: {
                  //         show: false
                  //     },
                  //     axisLine: {
                  //         show: false
                  //     },
                  //     axisTick: {
                  //         show: false
                  //     },
                  //     axisLabel: {
                  //         show: false
                  //     },
                  //     splitArea: {
                  //         show: true,
                  //         areaStyle: {
                  //             color: ["rgba(250,250,250,0.0)", "rgba(250,250,250,0.05)"]
                  //         }
                  //     }
                  // }
              ],
              series: [{
                  name: "农药",
                  type: "line",
                  yAxisIndex: 1, //使用的 y 轴的 index，在单个图表实例中存在多个 y轴的时候有用
                  smooth: false, //平滑曲线显示
                  showAllSymbol: true, //显示所有图形。
                  symbol: "emptyCircle", //标记的图形为实心圆
                  symbolSize: 10, //标记的大小
                  itemStyle: {
                      //折线拐点标志的样式
                      color: "#8957A1"
                  },
                  lineStyle: {
                      color: "#8957A1"
                  },
                  // areaStyle:{
                  //     color: 'transparent'
                  //     // color: "rgba(5,140,255, 0.2)"
                  // },
                  data: [1, 1.2, 3, 1, 1.5, 1]
              },
              {
                  name: "肥料",
                  type: "bar",
                  barWidth: '30%',
                  showBackground: false,
                  itemStyle: {
                      normal: {
                          color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                              offset: 0,
                              color: "red"
                          }, {
                              offset: 0,
                              color: "#1F0D5E"
                          }, {
                              offset: 1,
                              color: "#1480C2"
                          }
                          ])
                      }
                  },
                  label: {
                      normal: {
                          show: false,
                          textStyle: {
                              color: '#FFF'
                          },

                      }
                  },
                  data: [14, 16, 20, 7, 15, 11]
              }
              ]
        })
     },
     init2(){
       let xaxisData = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
       let yaxisData = [2220, 1682, 2791, 3000, 4090, 3230, 2910];
       let animationData = [["Sun", 2220], ["Mon", 1682], ["Tue", 2791], ["Wed", 3000], ["Thu", 4090], ["Fri", 3230], ["Sat", 2910]]

        let mychar = echarts.init(this.$refs.ondata2);
        mychar.setOption({
    backgroundColor: "#001829",
       grid: {
          top: "15%",
          left: "1%",
          right: "1%",
          bottom: "1%",
          containLabel: true,
       },
       tooltip: {
          trigger: "axis",
          confine: true,
          backgroundColor: "rgba(255, 132, 90, 0.3)",
          borderColor: "#FF845A",
          extraCssText: "box-shadow: 2px 2px 4px 0px rgba(255, 132, 90, 0.3);",
          textStyle: {
             fontSize: 16,
             color: "#fff",
             fontFamily: "PangMenZhengDao",
          },
          formatter: (params, ticket, callback) => {
             const item = params[0];
             return '终端数量' + "<br/>" + item.name + "：" + item.value + " 个";
          },
          axisPointer: {
             type: "line",
             lineStyle: {
                color: "#FF845A",
             },
          },
       },
       xAxis: {
          data: xaxisData,
          boundaryGap: true,
          axisLine: {
             show: true,
             symbol: ["none", "rect"],
             symbolSize: [6, 12],
             lineStyle: {
                width: 2,
                color: "#537DAA",
             },
          },
          axisTick: {
             show: false,
          },
       },
       yAxis: {
          name: "(个)",
          nameTextStyle: {
             color: "#AEC9FF",
             fontWeight: 400,
             fontSize: 14,
          },
          axisLabel: {
             color: "#AEC9FF",
          },
          axisLine: {
             show: true,
             symbol: ["none", "rect"],
             symbolSize: [6, 12],
             lineStyle: {
                width: 2,
                color: "#537DAA",
             },
          },
          axisTick: {
             show: false,
          },
          splitLine: {
             show: true,
             lineStyle: {
                color: "rgba(83, 125, 170, 0.2)",
             },
          },
       },
       series: [
          {
             type: "line",
             // smooth: true,
             lineWidth: 1.2,
             data: yaxisData,
             symbol: "none",
             itemStyle: {
                color: "rgba(114, 178, 255, 1)",
             },
             lineStyle: {
                width: 5,
                shadowBlur: 20,
                shadowColor: "#72B2FF",
                shadowOffsetY: 10,
             },
             areaStyle: {
                color: new echarts.graphic.LinearGradient(
                   0,
                   0,
                   0,
                   1,
                   [
                      {
                         offset: 0,
                         color: "rgba(114, 178, 255, 0.25)",
                      },
                      {
                         offset: 1,
                         color: "rgba(114, 178, 255, 0)",
                      },
                   ],
                   false
                ),
                opacity: 1,
             },
          },
          {
             type: "lines",
             coordinateSystem: "cartesian2d",
             data: [{
                coords: animationData
             }],
             zlevel: 1,
             polyline: true,
             symbol: "circle",
             effect:
             {
                show: true,
                trailLength: 0.4,
                symbol: "circle",
                period: 8,
                symbolSize: 8
             },
             lineStyle:
             {
                normal:
                {
                   color: '#64FFFF',
                   width: 0,
                   opacity: 0,
                   curveness: 0
                }
             }
          },
       ],
})
     }
   }




}
</script>

<style scoped>
.big{
  position: absolute;
    right: 0.6rem;
    z-index: 10000;
    top: 3rem;
  height: 80vh;
  width: 38rem;
}
.ondata{
   /* background: url("../assets/border.png") no-repeat; */
   position: absolute;
   width: 38rem;
   height: 19rem;
}
.ondata2{
  top: 20rem;
}
.out{
  animation: out 2s ;
  animation-fill-mode: forwards;
}
.in{
  animation: in 2s ;
  animation-fill-mode: forwards;
}
@keyframes out {
  from{
    transform: translate(0%);
  }
  to{
    transform: translate(100%);
  }
}

@keyframes in {
  from{
    transform: translate(100%);
  }
  to{
    transform: translate(0%);
  }
}
.imgarr{
  position: absolute;
  left: -2rem;
  height: 2rem;
  cursor: pointer;
}
.left{
  transform: rotateY(180deg);
}
.right{

}
</style>



